import Select, { components } from 'react-select';
import arrowSelect from './arrow_select.svg';

import './app-select-area.scss'
 
const options = [
    { value: 'водитель', label: 'водитель' },
    { value: 'кассир', label: 'кассир' },
    { value: 'пекарь', label: 'пекарь' },
    { value: 'повар', label: 'повар' },
    { value: 'приемщик', label: 'приемщик' },
    { value: 'продавец', label: 'продавец' },
];

const DropdownIndicator = (
  props: ElementConfig<typeof components.DropdownIndicator>
) => {
  return (
    <components.DropdownIndicator {...props}>
          <img src={arrowSelect} alt={"icon"}/>
    </components.DropdownIndicator>
  );
};
const SelectArea = ({change}) => {
    return <Select options={options}
                    placeholder='Выбирете...'
      components={{ DropdownIndicator }}
      onChange={change}
            />
}

export default SelectArea;