import React from "react";
// Import Swiper React components
import { Swiper, SwiperSlide } from "swiper/react";



import SlideJob from '../app-slide-job/app-slide-job'


import "swiper/swiper.scss";
import "swiper/swiper-vars.scss";

import SwiperCore, {
  Navigation
} from 'swiper';

import "./app-second-slider.scss"

import TitleBlock from '../app-title-block/app-title-block';

// install Swiper modules
SwiperCore.use([Navigation]);



const SecondSlider = ({data}) => {
    const sliderItems = data.map(item => {
        const { id, ...itemProps } = item;
        return (
             <SwiperSlide key={ id }>
                 <SlideJob {...itemProps}/>
             </SwiperSlide>
        )
    })
    return (
        <div className="second__slider_wrapper">
            <div className="container">
                <TitleBlock title="вакансии в гросс маркете"/>
                <Swiper
                    navigation={true}
                    slidesPerView={3}
                    spaceBetween={30}
                    breakpoints={{
                      300: {
                           slidesPerView: 1.25,
                           spaceBetween: 15,
                           centeredSlides: true
                      },
                      480: {
                          slidesPerView: 1,
                          spaceBetween : 0
                      },
                      600: {
                        slidesPerView: 1.6,
                      },
                      768: {
                        slidesPerView: 2.2,
                      },
                      800: {
                        slidesPerView: 2.3,
                      },
                      992: {
                        slidesPerView: 2.9,
                      },
                      1110: {
                        slidesPerView: 2.6,
                      },
                      1360: {
                        slidesPerView: 2.8,
                      },
                      1440: {
                        slidesPerView: 3,
                      },
                    }}
                >
                    { sliderItems }
                </Swiper>
            </div>
        </div>
    )
}


export default SecondSlider;