import { Link } from "react-router-dom";

import logo from './gross_market_logo.svg';
import './app-logo.scss';

const Logo = () => {
    return (
        <div>
            <Link to="/" className="logo_wrapper">
                <img src={logo} alt="logo"/>
                <p className="logo_text">гросс маркет</p>
            </Link>
        </div>
    )
}

export default Logo;