import './app-call-to.scss';
import { ReactComponent as PhoneIcon } from './icon_phone.svg';

const CallTo = (props) => {
    return (
       <div className="link_wrapper">
            <a className="link__phone animate__link_style" href={"tel:" + props.number}>
                <span className="phone_number">{props.number}</span> 
                <span className="phone_icon">
                    <PhoneIcon/>
                </span>
            </a>
       </div>
   )
}
export default CallTo;