import LabelCard from '../app-label-card/app-label-card';
import { Picture } from 'react-responsive-picture';

import './app-slide-banner.scss'

const SlideBanner = ({title, pic_larg, pic_mid, pic_small, location_style, name, job }) => {
    return (
        <div className="slide__wrapper">
            
            <div className="title__slide">
                {title}
            </div>
            <div className="img_wrapper">
                <Picture
                    sources = {[
                        {
                            srcSet: pic_small,
                            media: "(max-width: 680px)",
                        },
                        {
                            srcSet: pic_mid,
                            media: "(max-width: 1024px)",
                        },
                        {
                            srcSet: pic_larg,
                            type: "image/png",
                        }
                    ]}
                />
                <div className={"dec__content" + location_style}>
                    <LabelCard name={name}/>
                    <LabelCard name={job}/>
                </div>
            </div>
            
        </div>
    )
}

export default SlideBanner;