import {useState} from "react";

import { ReactComponent as CloseBtn } from './close_btn.svg';

import './app-popup.scss';

const Popup = ({ active, setActive }) => {
    
    const closePopUp = () => {
        document.body.classList.remove('modal');
        setActive(false);
    }
    const [scrollPos, setScrollPos] = useState(false);
   
    const setSctollBlock = e => {
        if (e.target.scrollTop !== 0) {
            setScrollPos(true)
        } else {
             setScrollPos(false)
        }
    }
    return (
        <div className={`${ active? "popup__wrapper active": "popup__wrapper" } ${scrollPos ? " scrolling" : null}`} onClick={closePopUp}>
            <div className="popup__content" onClick={e => e.stopPropagation()}>
                <div className="container">
                    <div className="close_popup_btn" onClick={closePopUp}>
                        <CloseBtn />
                    </div>
                    <h1 className="titel">Обработка данных</h1>
                    <div className="inner__content" onScroll={setSctollBlock}>
                        <ul className="question__list">
                            <li className="question__item">
                                <p className="text__qustion">1. Что регулирует настоящая политика конфиденциальности</p>
                                <p className="text__explaine">
                                    Настоящая политика конфиденциальности (далее — Политика) действует в отношении всей информации,
                                    включая персональные данные в понимании применимого законодательства (далее — «Персональная информация»),
                                    которую ООО «Гросс маркет» и/или его аффилированные лица, в том числе входящие в одну группу с
                                    ООО «Гросс маркет» (далее — «Гросс маркет»), могут получитьо Вас в процессе использования Вами любых сайтов, 
                                    программ, продуктов иили сервисов Гросс маркет (далее вместе  «Сервисы»), информацию о которых Гросс маркет
                                    может также получать Персональную информацию от своих партнеров (далее — «Партнеры»), сайты, программы, 
                                    продукты или сервисы которых Вы используете (например, от рекламодателей Гросс маркет или службами такси). 
                                    В таких случаях передача Персональной информации возможна только в случаях, установленных применимым 
                                    аконодательством, и осуществляется на основании специальных договоров между Гросс маркет и каждым из Партнеров.
                                </p>
                                <p className="text__explaine wraning_text">
                                    Пожалуйста, обратите внимание, что использование любого из Сайтов и/или Сервисов может регулироваться
                                    дополнительными условиями, которые могут вносить в настоящую Политику изменения и/или дополнения, и/или
                                    иметь специальные условия в отношении персональной информации, размещенные в соответствующих разделах
                                    документов для таких Сайтов /или Сервисов.
                                </p>    
                            </li>
                            <li className="question__item">
                                <p className="text__qustion">2. Кто обрабатывает информацию</p>
                                <p className="text__explaine">
                                    Для обеспечения использования Вами Сайтов и Сервисов Ваша Персональная информация собирается и используется 
                                    Яндексом, в том числе включая общество с ограниченной ответственностью «Гросс маркет», юридическое лицо,
                                    созданное по законодательству Российской Федерации и зарегистрированное по адресу: 123351, Россия,
                                    Москва, ул. Гроссова, д. 12 (ООО «Гросс маркет»), или его аффилированным лицом, предоставляющим 
                                    соответствующий Сервис в иных юрисдикциях. С информацией о том, какое лицо предоставляет тот или 
                                    иной Сервис, Вы можете ознакомиться в условиях использования соответствующего Сервиса.
                                </p>  
                            </li>
                            <li className="question__item">
                                <p className="text__qustion">3. Какова цель данной Политики</p>
                                <p className="text__explaine">
                                    Защита Вашей Персональной информации и Вашей конфиденциальности чрезвычайно важны для Гросс маркета. 
                                    Поэтому при использовании Вами Сайтов и Сервисов Гросс маркет защищает и обрабатывает Вашу Персональную 
                                    информацию в строгом соответствии с применимым законодательством.
                                </p>  
                            </li>
                            <li className="question__item">
                                <p className="text__qustion">4. Какую Персональную информацию о Вас собирает Гросс маркет</p>
                                <p className="text__explaine">
                                    Для обеспечения использования Вами Сайтов и Сервисов Ваша Персональная информация собирается и используется 
                                    Яндексом, в том числе включая общество с ограниченной ответственностью «Гросс маркет», юридическое лицо, 
                                    созданное по законодательству Российской Федерации и зарегистрированное по адресу: 123351, Россия, 
                                    Москва, ул. Гроссова, д. 12 (ООО «Гросс маркет»), или его аффилированным лицом, предоставляющим 
                                    соответствующий Сервис в иных юрисдикциях. С информацией о том, какое лицо предоставляет тот или иной
                                    Сервис, Вы можете ознакомиться в условиях использования соответствующего Сервиса.
                                </p>  
                            </li>
                        </ul>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Popup;